            left: 0;
            bottom: -35px;
            width: 100%;
            height: 45px;
            border-left: 363px solid #fff;
            border-top: 0 solid transparent;
            border-bottom: 30px solid transparent;
            z-index: 0;
        }
    }

}

.footer-yellow-shape-img {
