.masonary-gallery-wrapper {
    display: flex;
    flex-wrap: wrap;

    .big-image {
        overflow: hidden;
        border-radius: 10px;
        margin-right: 7.5px;
        margin-left: 7.5px;

        min-width: calc(100% - 15px);
        min-height: 272px;
        max-height: 272px;

        @include tablet {
            min-width: calc(40% - 15px);
        }

        @include desktops {
            min-height: 472px;
            max-height: 472px;
        }

        .single-image {
            width: 100%;
            height: 100%;
            margin: 0 0;
        }
    }

    .small-images {
        display: flex;
        flex-wrap: wrap;
        min-width: 100%;
        margin-top: 7.5px;

        @include tablet {
            margin-top: -7.5px;
            min-width: 60%;
        }

        .single-image {
            max-height: 128px;
            min-height: 128px;

            @include tablet {
                max-height: 128px;
                min-height: 128px;
            }

            @include desktops {
                max-height: 228px;
                min-height: 228px;
            }

            &:first-child {
                min-width: calc(50% - 15px);

                @include mobile-lg {
                    min-width: calc(35% - 15px);
                }
            }

            &:nth-child(2) {
                min-width: calc(50% - 15px);

                @include mobile-lg {
                    min-width: calc(25% - 15px);
                }
            }

            &:nth-child(3) {
                min-width: calc(50% - 15px);

                @include mobile-lg {
                    min-width: calc(40% - 15px);
                }
            }

            &:nth-child(4) {
                min-width: calc(50% - 15px);

                @include mobile-lg {
                    min-width: calc(35% - 15px);
                }
            }

            &:nth-child(5) {
                min-width: calc(100% - 15px);

                @include mobile-lg {
                    min-width: calc(65% - 15px);
                }
            }
        }
    }

    .single-image {
        background-size: cover;
        background-position: center;
        background-repeat: no-repeat;
        margin: 7.5px;
        border-radius: 10px;
        overflow: hidden;

        span {
            display: block;
            height: 100%;
            width: 100%;
            background-size: cover;
            background-position: center;
            background-repeat: no-repeat;
            transition: 0.4s;
            position: relative;
            will-change: transform;

            &:before {
                position: absolute;
                top: 0;
                left: 0;
                background: rgba(#000, 0.3);
                content: "";
                height: 100%;
                width: 100%;
                opacity: 0;
                transition: 0.4s;
                will-change: opacity;
            }
        }

        img {
            width: 100%;
        }

        &:hover {
            span {
                transform: scale(1.2);

                &:before {
                    opacity: 1;
                }
            }
        }
    }
}