.doc-sidebar--left{
  position: fixed;
  left: 0;
  top: 0;
  display: block!important;
  height: 100%!important;
  // width: 320px;
  background: #f7f9fc!important;
  padding-left: 30px;
  padding-right: 30px;
  padding-top: 30px;
  padding-bottom: 30px;
  overflow: unset!important;
  overflow-y: scroll!important;
  @include till-desktop{
    width: 285px;
    z-index: 999;
    left: -100%;
    transition: all 800ms cubic-bezier(0.785, 0.135, 0.15, 0.86);
    visibility: hidden;
  }
  &::-webkit-scrollbar {
    width: 10px;
    
  }

  &::-webkit-scrollbar-track {
    background: #f1f1f1;
  }
  &::-webkit-scrollbar-thumb {
    background: #888;
    border-radius: 5px;
  }
  &::-webkit-scrollbar-thumb:hover {
    background: #555;
  }
  &.collapse{
    
    &.show{
     @include till-desktop{
      left: 0%;
      visibility: visible;
     }
    }
  }
  &.collapsing{
    @include till-desktop{
      transition: height 0s,left 800ms cubic-bezier(0.785, 0.135, 0.15, 0.86);
      height: 100%;
      visibility: visible;
     }
  }
}

.sidebar-brand-block {
  text-align: center;
  padding-bottom: 30px;
  margin-bottom: 30px;
  border-bottom: 1px solid #ccc;
  a {
    width: 50px;
    @include desktops{
      width: 85px;
    }
  }
}
.sidebar-menu-group{
  .menu-title{
    font-weight: 700;
    line-height: 1.2;
    color: #2c3038;
    margin-bottom: 15px;
    font-size: 18px;
  }
}
.sidebar-menu{
  li{
    a{
      display: inline-block;
      color: #555a64;
      font-size: 15px;
      line-height: 22.5px;
      font-weight: 600;
      padding: 8px 0px;
      transition: all .2s ease-in-out;
      &:hover{
        text-decoration: none;
      }
    }
    &:not(:hover) {
      opacity: .5;
    }
  }
}